<template>
  <div id="edit-element">
    <h3>Edit Element</h3>
    <div class="row">
      <form @submit.prevent="updateElement" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input disabled type="text" v-model="element_id" required />
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required />
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="symbol" required />
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="standard_state" required />
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="year_discovered" required />
          </div>
        </div>

        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Back</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "edit-element",
  data() {
    return {
      element_id: null,
      name: null,
      symbol: null,
      standard_state: null,
      year_discovered: null,
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("elements")
      .where("element_id", "==", to.params.element_id)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next((vm) => {
            vm.element_id = doc.data().element_id;
            vm.name = doc.data().name;
            vm.symbol = doc.data().symbol;
            vm.standard_state = doc.data().standard_state;
            vm.year_discovered = doc.data().year_discovered;
          });
        });
      });
  },
  watch: {
    $route: "fetchData",
  },
  methods: {
    fetchData() {
      db.collection("elements")
        .where("element_id", "==", this.$route.params.element_id)
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.element_id = doc.data().element_id;
            this.name = doc.data().name;
            this.symbol = doc.data().symbol;
            this.standard_state = doc.data().standard_state;
            this.year_discovered = doc.data().year_discovered;
          });
        });
    },
    updateElement() {
      db.collection("elements")
        .where("element_id", "==", this.$route.params.element_id)
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            doc.ref
              .update({
                element_id: this.element_id,
                name: this.name,
                symbol: this.symbol,
                standard_state: this.standard_state,
                year_established: this.year_established,
              })
              .then(() => {
                this.$router.push({
                  name: "view-element",
                  params: { element_id: this.element_id },
                });
              });
          });
        });
    },
  },
};
</script>


