<template>
  <div id="view-element">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{name}}</h4>
      </li>
      <li class="collection-item">Element ID#: {{element_id}}</li>
      <li class="collection-item">Name: {{name}}</li>
      <li class="collection-item">Symbol: {{symbol}}</li>
      <li class="collection-item">Standard State: {{standard_state}}</li>
      <li class="collection-item">Year Discovered: {{year_discovered}}</li>
    </ul>

    <router-link to="/" class="btn purple">Back</router-link>
    <button @click="deleteElement" class="btn pink">Delete</button>

    <div class="fixed-action-btn">
      <router-link
        v-bind:to="{name: 'edit-element', params: {elemen: element_id}}"
        class="btn-floating btn-large blue"
      >
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "view-element",
  data() {
    return {
      element_id: null,
      name: null,
      symbol: null,
      standard_state: null,
      year_discovered: null,
    };
  },

  //Navigation gaurd that runs before the page loads
  beforeRouteEnter(to, from, next) {
    db.collection("elements")
      .where("element_id", "==", to.params.element_id)
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          next((vm) => {
            vm.element_id = doc.data().element_id;
            vm.name = doc.data().name;
            vm.symbol = doc.data().symbol;
            vm.standard_state = doc.data().standard_state;
            vm.year_discovered = doc.data().year_discovered;
          });
        });
      });
  },
  watch: {
    $route: "fetchData",
  },
  methods: {
    fetchData() {
      db.collection("elements")
        .where("element_id", "==", this.$route.params.element_id)
        .get()
        .then((querySnapshot) => {
          querySnapshot.forEach((doc) => {
            this.element_id = doc.data().element_id;
            this.name = doc.data().name;
            this.symbol = doc.data().symbol;
            this.standard_state = doc.data().standard_state;
            this.year_discovered = doc.data().year_discovered;
          });
        });
    },
    deleteElement() {
      if (confirm("Are you sure..?")) {
        db.collection("elements")
          .where("element_id", "==", this.$route.params.element_id)
          .get()
          .then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
              doc.ref.delete();
              this.$router.push("/");
            });
          });
      }
    },
  },
};
</script>


