<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>Elements</h4>
      </li>
      <li v-for="element in elements" v-bind:key="element.id" class="collection-item">
        <div class="chip">{{element.symbol}}</div>

        {{element.element_id}}:{{element.name}}
        <router-link
          class="secondary-content"
          v-bind:to="{name: 'view-element', params: {element_id: element.element_id}}"
        >
          <i class="fa fa-flask"></i>
        </router-link>
      </li>
    </ul>
    <div class="fixed-action-btn">
      <router-link to="/new" class="btn-floating btn-large green">
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "dashboard",
  data() {
    return {
      elements: [],
    };
  },
  created() {
    db.collection("elements")
      .orderBy("element_id")
      .get()
      .then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          const data = {
            id: doc.id,
            element_id: doc.data().element_id,
            name: doc.data().name,
            symbol: doc.data().symbol,
            standard_state: doc.data().standard_state,
            year_discovered: doc.data().year_discovered,
          };
          this.elements.push(data);
        });
      });
  },
};
</script>


