<template>
  <div id="new-element">
    <h3>New Element</h3>
    <div class="row">
      <form @submit.prevent="saveElement" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="element_id" required />
            <label>Element ID#</label>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="name" required />
            <label>Name</label>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="symbol" required />
            <label>Symbol</label>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="standard_state" required />
            <label>Standard State</label>
          </div>
        </div>

        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="year_discovered" required />
            <label>Year Discovered</label>
          </div>
        </div>

        <button type="submit" class="btn">Submit</button>
        <router-link to="/" class="btn grey">Back</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit";
export default {
  name: "new-element",
  data() {
    return {
      element_id: null,
      name: null,
      symbol: null,
      standard_state: null,
      year_discovered: null,
    };
  },
  methods: {
    saveElement() {
      db.collection("elements")
        .add({
          element_id: this.element_id,
          name: this.name,
          symbol: this.symbol,
          standard_state: this.standard_state,
          year_discovered: this.year_discovered,
        })
        .then((docRef) => {
          this.$router.push("/");
        })
        .catch((error) => console.log(err));
    },
  },
};
</script>


